import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { Routes, RouterModule } from '@angular/router';

import { IonicModule } from '@ionic/angular';

import { PontosPage } from './pontos.page';

const routes: Routes = [
  {
    path: 'pontos',
    component: PontosPage,
    children:[
      { path: 'atividades', loadChildren: './atividades/atividades.module#AtividadesPageModule' },
      { path: 'usuarios', loadChildren: './users/users.module#UsersPageModule' },
      { path: 'grupos', loadChildren: './grupos/grupos.module#GruposPageModule' },
    ]
  },
  {
    path:'',
    redirectTo:'usuarios',
    pathMatch:'full'
  }
];

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    IonicModule,
    RouterModule.forChild(routes)
  ],
  declarations: [PontosPage]
})
export class PontosPageModule {}
